.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 20px 0;
  background-color: #ffe26a;
  font-size: 30px;
}
.menu__list {
  list-style: none;
  padding-right: 170px;
}
.menu__link {
  padding-right: 30px;
  text-decoration: none;
  color: #16123f;
  transition: color 0.5s;
}
.menu__link:hover {
  color: #75c9b7;
}
.menu__logo {
  margin-left: 50px;
  background-image: url("../../img/logo.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 55px;
  width: 200px;
  cursor: pointer;
}/*# sourceMappingURL=menu.css.map */